<template>
    <div>
        <v-expansion-panels>
    <v-expansion-panel>
      <v-expansion-panel-header>
        <v-card-title class="text-center" style="display: block;">{{title}}</v-card-title>
      
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-form>
            <v-row>
                <v-col cols="12" md="6">
             <v-text-field
              v-model="category._categoryName"
              label="Service Name"
              
              outlined
              dense
              hide-details
            ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
             <v-text-field
              v-model="category._servcieDate"
              label="Date"
              readonly
              outlined
              dense
              hide-details
            ></v-text-field>
                </v-col>
            </v-row>
        </v-form>
      
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
    </div>
</template>
<script>
import axios from 'axios'
import { onMounted,ref,defineComponent,reactive } from '@vue/composition-api'

export default defineComponent({
    setup() {
        const fcm_send = onMounted(() => {
           setDate()
        })
        let title = ref('Add Service Category')
        let _categoryName = ref('')
        let _servcieDate = ref('')
        const category = reactive(
            {
             _categoryName: '',
             _servcieDate: ''
            }       
        )

        function setDate()
        {
            let currentDate = new Date();
            category._servcieDate = currentDate.toLocaleDateString("en-US")
        }
        
        return {
         title,
         category,
         _categoryName,
         _servcieDate,
         setDate
        }
    }
   
})
</script>
